import styled from "@emotion/styled"
import { MainMenu } from "./main-menu"
import VisionValue from "./assets/visionvalueblack.png"
import Phone from "./assets/phone.png"

export const NaviBar = () => {
    return <FloatBar>
        <Logo src={VisionValue} alt={'VISION VALUE'} />
        <MainMenuBox>
            <MainMenu />
        </MainMenuBox>
        <PhoneNumber>
            <img src={Phone} style={{ objectFit: 'cover' }} alt={'Phone Number'} />
            <span style={{fontSize: '1.89rem',color: 'white',paddingLeft: '1rem'}}>010-63159020</span>
        </PhoneNumber>
    </FloatBar>
}

const FloatBar = styled.div`
position: relative;
width: 181.6rem;
height: 8rem;
background-color: white;
overflow: hidden;
`

const Logo = styled.img`
position: absolute;
left: 37rem;
top: 2.7rem;
width: 16.4rem;
height: 3.2rem;
`

const MainMenuBox = styled.div`
position: absolute;
left: 63.3rem;
width: 58rem;
`

const PhoneNumber = styled.div`
display: flex;
position: absolute;
left: 126.7rem;
top: 0;
height: 8rem;
width: 24.2rem;
align-items: center;
justify-content: center;
background-color: #c9b17a;
`
