import styled from "@emotion/styled";
import photocircle from './assets/photocircle.png'

export const CenterBox = styled.div`
position: absolute;
left: 0;
width: 100%;
display: flex;
justify-content: center;
`

export const CenterDiv = styled.div`
position: relative;
left: 0;
width: 100%;
display: flex;
justify-content: center;
`

interface SectionTitleProps {
    top: string
    title: string
    subTitleLeft: string
    subTitleRight: string
}

export const SectionTitle = (props: SectionTitleProps) => {
    const { top, title, subTitleLeft, subTitleRight } = props
    return <CenterBox style={{ top: top, display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
        <h2 style={{ letterSpacing: '0.8rem', marginBottom: '0' }}>{title}</h2>
        <div style={{ fontFamily: 'SY-Bold', fontSize: '2.4rem', fontWeight: 'bold' }}>
            <span style={{ color: '#c5c5c5', paddingRight: '0.25rem' }} >{subTitleLeft}</span>
            <GoldenSpan style={{ paddingLeft: '0.25rem' }} >{subTitleRight}</GoldenSpan>
        </div>
    </CenterBox>
}

interface BusinessSummaryProps {
    img: string
    title: string
    content: string
}

export const BusinessSummary = (props: BusinessSummaryProps) => {
    const { img, title, content } = props
    return <OneBusiness>
        <MBImg src={img} alt={title} />
        <MBTile>{title}</MBTile>
        <MBLine />
        <MBContent>{content}</MBContent>
    </OneBusiness>
}

const OneBusiness = styled.div`
width: 26rem;
display: flex;
flex-direction: column;
align-items: center;
`

const MBImg = styled.img`
object-fit: cover;
`
const MBTile = styled.h3`
margin-top: 4rem;
font-family: Source Han Sans Regular;
font-size: 2rem;
color: #666666;
font-weight: bold;
`

const MBLine = styled.div`
position: relative;
width: 6rem;
height: 2px;
border-top: 2px solid #666666;
margin-top: 1rem;
`

const MBContent = styled.span`
margin-top: 2.8rem;
font-family: Source Han Sans Normal;
font-size: 2rem;
color: #666666;
`

interface SoulFigureSummaryProps {
    img: string
    name: string
    title: string
    content: string
}


export const SoulFigureSummary = (props: SoulFigureSummaryProps) => {
    const { img, name, title, content } = props

    return <OneBusiness style={{ width: '32rem' }}>
        <div style={{ position: 'relative', width: '20.7rem', height: '20.7rem', marginBottom: '1.5rem' }}>
            <img src={photocircle} alt={''} style={{ position: 'absolute', left: '0', top: '0', objectFit: 'cover' }} />
            <img src={img} alt={name} style={{ position: 'absolute', left: '0.95rem', top: '0.95rem', objectFit: 'cover' }} />
        </div>
        <div>
            <span style={{ fontFamily: 'SY-Regular', fontSize: '2rem', fontWeight: 'bold', paddingRight: '0.5rem' }}>{name}</span>
            <span style={{ fontFamily: 'SY-Regular', fontSize: '2rem', fontWeight: 'bold', paddingLeft: '0.5rem', color: '#d2bf92' }}>{title}</span>
        </div>
        <MBContent style={{ fontSize: '1.6rem', lineHeight: '3rem', marginTop: '1.5rem' }}>{content}</MBContent>
    </OneBusiness>
}

export const ContentSpan = styled.span`
font-family: Source Han Sans Medium;
font-size: 1.7rem;
line-height: 4rem;
`

export const GoldenSpan = styled.span`
color: #d2bf92;
`